.add-people-form {
  background-color: #eee;
  padding: 40px 25px;
  text-align: center;
}
.add-people-form .title {
  margin: 0 auto 20px;
}
.add-people-form form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.add-people-form form input {
  margin: 5px auto;
}
.add-people-form form button {
  padding: 10px 50px;
  border-radius: 5px;
  background-color: #76a876;
  color: white;
  border: none;
  box-shadow: 0px 0px 5px rgba(0, 128, 0, 0.25);
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
}