@charset "UTF-8";
.app {
  font-family: "Montserrat", "Open Sans", Arial, Helvetica, sans-serif;
}
.app .content {
  display: flex;
  justify-content: space-around;
  align-items: initial;
  flex-wrap: wrap-reverse;
}

h1 {
  text-align: center;
  font-size: 3rem;
}

hr {
  margin: 3rem auto;
}

input {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.message-container {
  width: 90%;
  border-radius: 5px;
  border: 2px solid rgba(255, 255, 255, 0);
  font-size: 15px;
  font-weight: bold;
  padding: 1rem;
  margin: 1rem auto;
}
.message-container.success-message {
  background-color: rgba(57, 135, 54, 0.2117647059);
  color: green;
  border-color: green;
}
